html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Jost", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Jost", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.light {
  font-weight: 300 !important;
}

.bold {
  font-weight: 500 !important;
}

/** Media Queries **/
.header {
  background-color: #ecf0f0;
  box-shadow: 0.1rem 0.2rem 0.3rem #9e9b9b;
  text-align: center;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.vgral {
  color: #1E8800;
}

.navegacion-principal {
  z-index: 10;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}
.navegacion-principal a {
  display: block;
  color: #383F45;
  font-size: 2.2rem;
  font-weight: 500;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin-bottom: 0;
  }
}
.navegacion-principal a:hover {
  color: #1E8800;
}

.navegacion-secundaria {
  z-index: 10;
}
@media (min-width: 768px) {
  .navegacion-secundaria {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-secundaria a {
  display: block;
  color: #1E8800;
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-secundaria a {
    margin-bottom: 0;
  }
}
.navegacion-secundaria a:hover {
  color: #fff;
}

.logos {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .logos {
    margin-bottom: 0;
  }
}

.botones {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .botones {
    display: flex;
    justify-content: space-evenly;
  }
}

.boton {
  background-color: #1E8800;
  color: #fff;
  font-weight: 600;
  padding: 2rem;
  border-radius: 0.5rem;
}
.boton:hover {
  background-color: #8D919C;
  color: #fff;
}
.boton a {
  font-size: 3rem !important;
}

.boton2 {
  border: solid 3px #1E8800;
  color: #383F45;
  font-weight: 600;
  padding: 2rem 3rem;
  border-radius: 0.5rem;
}
.boton2:hover {
  background-color: #1E8800;
  color: #fff;
}

.lang-inact {
  color: #8D919C;
  font-weight: 600;
}

.lang-act {
  color: #00E276;
  font-weight: 600;
}
.lang-act:hover {
  color: #8D919C;
}

.check {
  position: relative;
  width: 5rem;
}
.check:before {
  content: "";
  position: absolute;
  width: 5rem;
  height: 2.5rem;
  background: #656666;
  border-radius: 2.5rem;
}
.check:after {
  content: "";
  position: absolute;
  width: 2.5rem;
  border-radius: 3rem;
  background: #fff;
  transition: 0.25s;
  border: 2px solid #656666;
  z-index: 10;
}
.check:checked:before {
  background: #00E276;
}

.check:checked:after {
  left: 2rem;
  border: 2px solid #00E276;
}

.flags {
  width: 8rem;
  display: flex;
  margin: 0 auto;
  justify-content: center;
}

.flags_item {
  width: 3rem;
}

.flags_img {
  cursor: pointer;
}

.blanco {
  color: #fff;
  text-shadow: 3px 3px 3px #333;
}

.inicio {
  background-color: #656666;
}

.division {
  margin: 5rem 0;
  color: #fff;
}

.div-menu {
  display: flex;
}

.activo {
  color: #1E8800 !important;
}

.mexiwear-logo {
  width: 30rem;
}

.berep-logo img {
  width: 10rem;
}

.hidden {
  display: none;
}

.chico {
  width: 20rem;
  padding: 1rem 0;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.mobile-menu {
  display: flex;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  display: none;
}

/** Media Queries **/
.footer {
  background-color: #383F45;
