}

/** Media Queries **/
.nosotros {
  display: grid;
}
@media (min-width: 768px) {
  .nosotros {
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    padding: 3rem 0;
  }
}

.quienes {
  margin: 3rem 0 2rem 0;
  text-align: center;
  background-color: #1E8800;
  border-radius: 0.5rem;
  color: #fff;
  padding: 1rem;
}

.contenido-nosotros {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #1E8800;
