}
@media (min-width: 768px) {
  .contenido-nosotros {
    margin-top: 0;
  }
}
.contenido-nosotros h3 {
  margin-bottom: 1rem;
  text-align: center;
}
.contenido-nosotros p {
  text-align: justify;
}

.mvv {
  display: grid;
  justify-content: center;
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .mvv {
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
    padding: 3rem 0;
  }
}
.mvv p {
  text-align: center;
  color: #fff;
}

.apartado {
  background-color: #1E8800;
  border-radius: 2rem;
  padding: 1.5rem;
