/** Media Queries **/
.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(83, 181, 132);
  background: linear-gradient(180deg, rgba(6, 255, 119, 0.3) 0%, rgba(0, 63, 69, 0.9) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: #fff;
  margin: 0;
  text-align: center;
}
.contenido-video p {
  background-color: rgba(0, 63, 69, 0.9);
  color: #fff;
  padding: 1.5rem 1.5rem;
  border-radius: 1rem;
  text-align: center;
  line-height: 2.7rem;
}